@media (min-width: 767px) {
  .menu__list {
    align-items: center;
    display: flex;
  }

  .menu__list > li {
    padding: 10px 0;
  }

  .menu__sub--list {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all .3s;
    transform: translate(0, 10%);
  }
}

@media (max-width: 767px) {
  .menu__icon {
    z-index: 5;
    width: 30px;
    height: 18px;
    cursor: pointer;
    display: block;
    position: relative;
  }

  .menu__icon span, .menu__icon:before, .menu__icon:after {
    height: 10%;
    width: 100%;
    background-color: #2e0049;
    transition: all .3s;
    position: absolute;
    left: 0;
  }

  .menu__icon:before, .menu__icon:after {
    content: "";
  }

  .menu__icon:before {
    top: 0;
  }

  .menu__icon:after {
    bottom: 0;
  }

  .menu__icon span {
    top: 50%;
    transform: scale(1)translate(0, -50%);
  }

  .menu__icon._active span {
    transform: scale(0)translate(0, -50%);
  }

  .menu__icon._active:before {
    top: 50%;
    transform: rotate(-45deg)translate(0, -50%);
  }

  .menu__icon._active:after {
    bottom: 50%;
    transform: rotate(45deg)translate(0, 50%);
  }

  .menu__body {
    width: 100%;
    height: 100%;
    background-color: #83ffd6;
    padding: 100px 30px 30px;
    transition: left .5s;
    position: fixed;
    top: 0;
    left: -100%;
    overflow: auto;
  }

  .menu__body._active {
    left: 0;
  }

  .menu__body:before {
    content: "";
    width: 100%;
    height: 70px;
    z-index: 2;
    background-color: #0ff;
    position: fixed;
    top: 0;
    left: 0;
  }

  .menu__list > li {
    flex-wrap: wrap;
    margin: 0 0 30px;
  }

  .menu__list > li:last-child {
    margin-bottom: 0;
  }

  .menu__list > li._active .menu__sub--list {
    display: block;
  }

  .menu__link {
    font-size: 24px;
  }

  .menu__sub--list {
    background-color: #fff;
    flex: 100%;
    margin-top: 20px;
    display: none;
    position: relative;
  }

  .menu__sub--link {
    color: #1e00ff;
    font-size: 20px;
  }
}

